#typography {
  display: flex;
  flex-flow: column;
  width: 100%;
  height: 100%;
  user-select: none;
}

#typography h2 {
  margin: 16px 20px 0px;
  box-sizing: border-box;
  line-height: 1.1;
  font-size: 26px;
  font-weight: 500;
}

#typography h3 {
  margin: 10px 0px;
  box-sizing: border-box;
  line-height: 1.2;
  font-size: 16px;
  font-weight: 700;
}

#typography h4 {
  margin: 6px 0px;
  box-sizing: border-box;
  line-height: 1.1;
  font-size: 12px;
  font-weight: 700;
}

#typography section {
  padding: 15px 20px;
}

#typography main {
  flex: 1 1 0%;
  overflow: auto;
  outline: none;
  position: relative;
  height: 100%;
  overflow-y: scroll;
}

#font-section {
  padding: 10px 20px 20px;
}

#font-family-subsection{
    width:100%;
}

#font-family-subsection x-select{
    width:400px;
}

#font-face-box{
    width:100%;
}

#font-face-box x-button{
    padding: 0px 43px;
    height: 28px;
    flex: 1 1 0%;
}

#font-size-box {
  width: 100%;
}

#font-size-slider {
  flex: 1 1 0%;
}

#font-size-input {
  margin-left: 6px;
  width: 58px;
}

#text-decoration-buttons {
  width: 100%;
}

#text-decoration-buttons x-button {
  padding: 0px 7px;
  height: 28px;
  flex: 1 1 0%;
}

#text-decoration-buttons x-label {
  font-weight: 600;
  font-size: 12px;
  text-align: center;
}

#underline-button x-label {
  text-decoration: underline;
}

#line-through-button x-label {
  text-decoration: line-through;
}

#overline-button x-label {
  text-decoration: overline;
  line-height: 2;
}

#text-transform-box {
  width: 100%;
  margin-top: 14px;
}

#text-transform-buttons x-button {
  padding: 0px 5px;
  height: 28px;
  flex: 1 1 0%;
}

#text-transform-buttons x-label {
  font-weight: 600;
  font-size: 12px;
  text-align: center;
}

#text-anchor-box {
  margin-top: 14px;
}

#text-anchor-buttons x-button {
  flex: 1 1 0%;
}

#text-anchor-buttons x-button x-label {
  font-size: 12px;
}

#letter-spacing-box {
  margin-bottom: 6px;
}

#letter-spacing-inner-box {
  width: 100%;
}

#letter-spacing-slider {
  flex: 1 1 0%;
}

#letter-spacing-input {
  margin-left: 6px;
  width: 58px;
}

#word-spacing-inner-box {
  width: 100%;
}

#word-spacing-slider {
  flex: 1 1 0%;
}

#word-spacing-input {
  margin-left: 6px;
  width: 58px;
}

#line-spacing-inner-box {
  width: 100%;
}

#line-spacing-slider {
  flex: 1 1 0%;
}

#line-spacing-input {
  margin-left: 6px;
  width: 58px;
}
