#fill h2{
    margin: 16px 20px 0px;
}

#paint-section{
    padding: 0px 20px 20px;
}

#opacity-section{
    padding: 14px 20px 20px;
}

#opacity-section{
    margin:  0 0 10px 0px;
    box-sizing: border-box;
    line-height: 1.2;
    font-size: 16px;
    font-weight: 700;
}

#opacity-slider {
    flex: 1 1 0%;
}

#opacity-section .box{
    display: flex;
    box-sizing: border-box;
    align-items: center;
    justify-content: flex-start;
}


#opacity-input {
    margin-left: 6px;
    width: 58px;
}