#objects{
    display: flex;
    flex-flow: column;
    width: 100%;
    height: 100%;
}

#objects header{
    padding: 16px 20px;
    border-bottom-width: 1px;
    border-bottom-style: solid;
    width:100%;
    border-color: #dcdcdc;;
}

#objects header h2 {
    margin: 0px;
    line-height: 1;
}

#objects-content{
    width:100%;
    height:calc(100% - 58px);
    flex-flow: column;
    background-color: #ffffff;
}

#objects-main{
    flex: 1 1 0%;
    padding: 4px 0px;
    overflow: auto;
    box-sizing: border-box;
    outline: none;
    position: relative;
}