#toolbar {
  position: relative;
  background-color: #f5f5f5;
}

#buttons {
  position: relative;
  width: fit-content;
  margin-top: -1px;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  box-sizing: border-box;
  background: inherit;
  overflow: auto;
}

.other-shape {
  top: 10px;
  height: calc(100% - 20px);
  left: 38px;
  width: 214px;
  transform: translateX(10px);
  position: absolute;
  transition: transform 0.2s cubic-bezier(0.4, 0, 0.2, 1) 0s;
  border: 1px solid #d1d1d1;
  border-radius: 12px;
  background-color: #ffffff;
  will-change: transform;
  z-index: 1;
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.15);
  overflow-y: auto;
}

.other-shape header {
  padding: 10px 0px 0px;
}

.other-shape h2 {
  margin: 0px;
  padding: 0px 14px;
}

.other-shape h3{
  margin: 0 0 10px 0px;
  box-sizing: border-box;
  line-height: 1.2;
  font-size: 16px;
  font-weight: 700;
}

#buttons .modal-icon {
    position: absolute;
    right: 3px;
    bottom: 3px;
    width: 6px;
    height: 6px;
}