#arrangement {
  display: flex;
  flex-flow: column;
  width: 100%;
  height: 100%;
  user-select: none;
}

#arrangement h3 {
  margin: 10px 0px;
  box-sizing: border-box;
  line-height: 1.2;
  font-size: 16px;
  font-weight: 700;
}

#arrangement main {
  flex: 1 1 0%;
  overflow: auto;
  outline: none;
  position: relative;
}

#arrangement h2 {
  margin: 16px 20px 0px;
  box-sizing: border-box;
  line-height: 1.1;
  font-size: 26px;
  font-weight: 500;
}

#align-section {
  padding: 10px 20px 18px;
}

#align-section x-button {
  width: 32px;
  height: 32px;
  margin: 0px 0px 5px;
  padding: 0px;
}

#transform-section {
  padding: 16px 20px 18px;
}

#transform-section x-button {
  height: 30px;
  margin-right: 11px;
  flex: 1 1 0%;
}

#order-section {
  padding: 16px 20px 18px;
}

#order-section x-button {
  height: 30px;
  margin-right: 11px;
  flex: 1 1 0%;
}

#grouping-section {
  padding: 16px 20px 18px;
}

#grouping-section x-button {
  height: 30px;
  margin-right: 11px;
  flex: 1 1 0%;
}

#order-section #order-buttons-2 {
  margin-top: 11px;
}
