#geometry {
  display: flex;
  flex-flow: column;
  width: 100%;
  height: 100%;
  user-select: none;
}

#geometry main {
  flex: 1 1 0%;
  overflow: auto;
  outline: none;
  position: relative;
}

#geometry h2 {
  margin: 16px 20px -8px;
  box-sizing: border-box;
  line-height: 1.1;
  font-size: 26px;
  font-weight: 500;
}

.geometry-content {
  display: block;
  width: 100%;
  padding: 18px 20px;
  box-sizing: border-box;
}

#position-section {
  display: flex;
  margin-top: 14px;
}

#position-section #x-box {
  flex: 1 1 0%;
  margin-right: 13px;
}

#position-section #x-box h4 {
  margin: 6px 0px;
  box-sizing: border-box;
  line-height: 1.1;
  font-size: 12px;
  font-weight: 700;
}

#position-section #y-box {
  flex: 1 1 0%;
  margin-left: 13px;
}

#position-section #y-box h4 {
  margin: 6px 0px;
  box-sizing: border-box;
  line-height: 1.1;
  font-size: 12px;
  font-weight: 700;
}

#position-section x-numberinput {
  width: 100%;
}
