#history{
    display: flex;
    flex-flow: column;
    width: 100%;
    height: 100%;
}

#history header{
    padding: 16px 20px;
    border-bottom-width: 1px;
    border-bottom-style: solid;
    width:100%;
    border-color: #dcdcdc;;
}

#history header h2 {
    margin: 0px;
    line-height: 1;
}

#history-content{
    width:100%;
    height:calc(100% - 58px);
    flex-flow: column;
    background-color: #ffffff;
}

#history-main{
    flex: 1 1 0%;
    padding: 4px 0px;
    overflow: auto;
    box-sizing: border-box;
    outline: none;
    position: relative;
}

#history .item{
    display: flex;
    align-items: center;
    padding: 3px 20px;
    font-size: 13px;
}

#history #items .item x-icon {
    width: 17px;
    height: 17px;
    min-width: 17px;
}

#items .item .label {
    margin-left: 10px;
    white-space: pre;
    overflow: hidden;
    text-overflow: ellipsis;
}

#items .item{
    border-top:1px solid #dcdcdc;
}

#items .item:first-child{
    border-top:none;
}

#items .item-active{
    color:#ffffff;
    background-color: #3584e4;
}