#three-box{
    display: flex;
    flex-flow: column;
    width: 100%;
    height: 100%;
}

#three-box header{
    padding: 16px 20px;
    border-bottom-width: 1px;
    border-bottom-style: solid;
    width:100%;
    border-color: #dcdcdc;;
}

#three-box header h2 {
    margin: 0px;
    line-height: 1;
}

#three-box-content{
    width:100%;
    height:calc(100% - 58px);
    flex-flow: column;
    background-color: #ffffff;
}

#three-box-main{
    display: block;
    width: 100%;
    padding: 18px 20px;
    box-sizing: border-box;
}

#length-input,#width-input,#height-input{
    width:100%;
    max-width: 100%;
}
#blade-btn{
    margin-top:20px;
}
#blade-btn x-button{
    display: block !important;
    margin:0 auto !important;
}
