.flex {
  display: flex;
}

.flex-justify-center {
  justify-content: center;
}

.flex-justify-around {
  justify-content: space-around;
}

.flex-justify-between {
  justify-content: space-between;
}

.flex-justify-start {
  justify-content: flex-start;
}

.flex-justify-end {
  justify-content: flex-end;
}

.flex-align-center {
  align-items: center;
}

body , #root{
  width:100%;
  height:100%;
}

.App{
  display: flex;
  flex-flow: column;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

#board{
  flex: 1 1 0%;
  contain: strict;
  width:100%;
  height:100%;
}

#editor{
  width: 100%;
  height:100%;
  flex: 1 1 0%;
}

.toggle-3d-wrapper {
  background-color: #ffffff;
  box-shadow: rgb(0 0 0 / 17%) 0px 3px 10px 0px;
  cursor: pointer;
  position: fixed;
  left: 80px;
  bottom: 12px;
  z-index: 899;
  border-radius: 4px;
}

.toggle-3d-wrapper .toggle-3d-item {
  position: relative;
  color: #333333;
  min-width: 34px;
  min-height: 30px;
  
}

.toggle-3d-item-active  span {
  color: #ffffff;
}

.toggle-3d-wrapper .toggle-3d-item span {
  transition: all 200ms;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

.toggle-3d-wrapper .disabled {
  opacity: 0.25;
  cursor: not-allowed;
  pointer-events: none;
}

.toggle-3d-item-active:nth-child(1)::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  animation: move2d 350ms ease-out;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  border-radius: 4px;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  background-color: #3584e4;
}
.toggle-3d-item-active:nth-child(2)::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  animation: move3d 350ms ease-out;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  border-radius: 4px;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  background-color: #3584e4;
}

@keyframes move2d {
  0% {
    transform: translateX(100%);
  }
  60% {
    transform: translateX(0);
  }
  70% {
    transform: translateX(15%);
  }
  80% {
    transform: translateX(0);
  }
  90% {
    transform: translateX(7%);
  }
  100% {
    transform: translateX(0);
  }
}
@keyframes move3d {
  0% {
    transform: translateX(-100%);
  }
  60% {
    transform: translateX(0);
  }
  70% {
    transform: translateX(-15%);
  }
  80% {
    transform: translateX(0);
  }
  90% {
    transform: translateX(-7%);
  }
  100% {
    transform: translateX(0);
  }
}
